* {
  box-sizing: border-box;
  margin: 0;
}

:root {
  --grey-dark:#343a40;
  --Orange:Orange;
}

.navbar {
  position: sticky;
}
.navbar .navbar-nav {
  font-size: 1.125rem;
}

#home {
  margin: 2.5rem 0;
}
#home .display-1 {
  margin-bottom: 0.9375rem;
}
#home .d-block {
  height: 37.5rem;
  width: 100%;
}

#about #img-about-1 {
  height: 31.25rem;
  width: 25rem;
}

#skills .card {
  width: 9.375rem;
  text-align: center;
  color: white;
  background-color: var(--grey-dark);
  margin-left: 0.9375rem;
  margin-top: 1.875rem;
}
#skills .card i {
  font-size: 4.375rem;
}

#project .card {
  padding-top: 1.25rem;
  width: 21.875rem;
  text-align: center;
  margin-left: 1.25rem;
  margin-top: 1.875rem;
  background-color: var(--grey-dark);
  color: white;
}

hr {
  margin-bottom: 0.625rem;
}

#navbar.white {
  background-color: white;
}